import "./styles.css";

const onClickAdd = () => {
  // テキストボックスの値を取得し、初期化する
  const inputText = document.getElementById("add-text").value;
  document.getElementById("add-text").value = "";

  createIncompleteList(inputText);
};

//未完了リストから指定の要素を削除
const deleteFromIncompleteList = (target) => {
  document.getElementById("incompleted-list").removeChild(target);
};

document
  .getElementById("add-button")
  .addEventListener("click", () => onClickAdd());

//未完了リストに追加する関数
const createIncompleteList = (text) => {
  // div生成
  const div = document.createElement("div");
  div.className = "list-row";

  //pタグ作成
  const p = document.createElement("p");
  p.innerText = text;

  // liタグ作成
  const li = document.createElement("li");

  // button(完了)タグ生成
  const completeButton = document.createElement("button");
  completeButton.innerText = "完了";
  completeButton.addEventListener("click", () => {
    //完了リストに追加する要素
    const addTarget = completeButton.parentNode;
    addTarget.removeChild(completeButton);
    addTarget.removeChild(deleteButton);
    addTarget.appendChild(backButton);

    // 押された完了ボタンの親タグ(li)を未完了リストから完了リストに移動
    // deleteFromIncompleteList(addTarget.parentNode);
    document.getElementById("completed-list").appendChild(addTarget.parentNode);
  });

  // button(削除)タグ生成
  const deleteButton = document.createElement("button");
  deleteButton.innerText = "削除";
  deleteButton.addEventListener("click", () => {
    // 押された削除ボタンの親タグ(li)を未完了リストから削除
    deleteFromIncompleteList(deleteButton.parentNode.parentNode);
  });

  // button(戻す)タグ生成
  const backButton = document.createElement("button");
  backButton.innerText = "戻す";
  backButton.addEventListener("click", () => {
    //未完了リストに追加する要素
    const addTarget = backButton.parentNode;
    addTarget.appendChild(completeButton);
    addTarget.appendChild(deleteButton);
    addTarget.removeChild(backButton);

    document
      .getElementById("incompleted-list")
      .appendChild(addTarget.parentNode);
  });

  //liタグの子要素に各要素を設定
  div.appendChild(p);
  div.appendChild(completeButton);
  div.appendChild(deleteButton);
  li.appendChild(div);

  // 未完了リストに追加
  document.getElementById("incompleted-list").appendChild(li);
};
